.profile__name {
  padding: 0;
  margin: 0;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  grid-row: 1;
  grid-column: 1;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 900px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
  }  
        
}